export const mutations = {

  getData(s, data) {
    s.movieData = data;
    // console.log('s.movieData=>',s.movieData);
  },

  resetData(s) {
    s.movieData = {
      title:'',
      pubdate:''
    }
  },

  //改变座位状态
  changeChairData(s, data) {
    s.chairDatas[data.x - 1][data.y - 1].type = data.type;
  },

  //添加用户选择座位数据
  addUserChairData(s, data) {
    s.userChairData.unshift(data);
    // console.log('s.userChairData=>',s.userChairData);
  },

  //删除用户选择的座位
  delUserChairData(s, data) {
    
    for (let i = 0; i < s.userChairData.length; i++) {
      if (s.userChairData[i].x == data.x && s.userChairData[i].y == data.y) {
        s.userChairData.splice(i, 1);
        break;
      }
    }
  },

  //修改显示票据ul的宽度
  modifyUlWidth(s, width) {
    s.width = width;
    // console.log(' s.width=>', s.width);
  }

};