export const mutations = {
    getDetailData(s,o){
        s.comingMovieData = o.concat();
        // console.log('s.comingMovieData=>',s.comingMovieData);

        s.detailData.data = s.comingMovieData.
        slice(s.detailData.start,s.detailData.end);

        s.detailData.start = s.detailData.end;
        s.detailData.end += 1;

        // console.log('s.detailData=>',s.detailData);
        
        s.detailData.data.forEach(v => {
            // console.log('v=>',v.id);
            s.detailDataId.push(v.id);
        });

        // console.log('s.detailDataId=>',s.detailDataId);
      
    },

    getFindDataData(s,o){
        s.a = o;
        s.findData.push(s.a);
        // console.log('s.findData=>',s.findData);

    }
  
}