export const mutations = {
    
    // 获取修改电影数据
    changeMovieData(s,r){

        if (s.isInit) {
            s.isInit = false; //请求之后就将初始化关掉
        }

        if (s.isInitial) {
            s.isInitial = false;
        }
      
        // 懒加载数量不足10条之后仅加载这一次便退出
        if(r.data.subjects.length <10){
            s.isHas = false;
        }
        // s.movieData = result; // s=>state, r=>result(Api接口中数据)
        
        s.movieData.data.subjects.push(...r.data.subjects); // ...相当于一个一个往里面推送
        // console.log('state.movieData ==> ', s.movieData);

        s.params.start += s.params.count;

        //console.log('state.params ==> ', s.params); //此时输出会显示 。。count: 20  start: 10
    },

    // 懒加载修改movieListData
    changeMovieList(s, o) {
        // s. movieList = o;
        s.movieListData.movieList = o.movieList;
        s.movieListData.movieListH = o.movieListH;
    },

    // 修改定位城市
    changeLocationCity(s, city) {
      s.params.city = city;
    //console.log('s.params.city ==> ', s.params.city);
    }

}