import routesComponent from './routesComponent'

// export const routes = [
  export default [
     //注册
    {
      path: '/register',
      name: 'register',
      component: routesComponent.registerComponent,
    },
    {
      path: '/phoneRegister',
      name: 'phoneRegister',
      component: routesComponent.phoneRegisterComponent
    },
    {
      path: '/login',
      name: 'login',
      component: routesComponent.loginComponent
    },
    {
      path: '/forgetPwd',
      name: 'forgetPwd',
      component: routesComponent.forgetPwdComponent
    },

    {
      path: '/index',
      name: 'index',
      component: routesComponent.indexComponent,
    
      // 二级路由
      children:[
        // 首页
        {
          path: '/home',
          name: 'home',
          component: routesComponent.homeComponent
        },
        // 电影
        {
          path: '/movie',
          name: 'movie',
          component: routesComponent.movieComponent,

          // 三级路由
          children:[
            // 电影列表
            {
              path: '/movielist',
              name: 'movielist',
              component: routesComponent.movielistComponent
            },

            // 影院
            {
              path: '/cinema',
              name: 'cinema',
              component: routesComponent.cinemaComponent
            },
            
            // 即将上映
            {
              path: '/coming',
              name: 'coming',
              component: routesComponent.comingComponent
            }
          ]
        },
        // 发现
        {
          path: '/find',
          name: 'find',
          component: routesComponent.findComponent
        },
        // 演出
        {
          path: '/show',
          name: 'show',
          component: routesComponent.showComponent
        },
        // 我的
        {
          path: '/mine',
          name: 'mine',
          component: routesComponent.mineComponent
        },
      ]
    },

    // 详情页一级路由，该路径携带参数，因为会跳转到不同的详情页, (格式是 /:路由参数)
    {
      path: '/movieDetail/:id',
      name: 'movieDetail',
      component: routesComponent.movieDetailComponent
    },

    // 收藏
    {
      path: '/likeMovie',
      name: 'likeMovie',
      component: routesComponent.likeMovieComponent
    },

    // 购票页面
    {
      path: '/buyTickets',
      name: 'buyTickets',
      component: routesComponent.buyticketsComponent
    },

    // 搜索页面
    {
      path: '/searchMovie',
      name: 'searchMovie',
      component: routesComponent.searchMovieComponent
    },

    // 最新预告  首页跳转
    {
      path: '/teaser',
      name: 'teaser',
      component: routesComponent.teaserComponent
    },

    // 事实票房
    {
      path: '/boxOffice',
      name: 'boxOffice',
      component: routesComponent.boxOfficeComponent
    },

    // 历史榜单
    {
      path: '/rankingList',
      name: 'rankingList',
      component: routesComponent.rankingListComponent
    },
    // 周边商城
    {
      path: '/mall',
      name: 'mall',
      component: routesComponent.mallComponent
    },

    // pay
    {
      path: '/tickets',
      name: 'tickets',
      component: routesComponent.ticketsComponent
    },
    
    {
      path: '*',
      redirect: {name: 'home'}
    }
  
  ]