export const mutations = {
    getMDetailData(s, data) {
      s.movieDetailData = data;

      // console.log('s.movieDetailData ==> ', s.movieDetailData);
      s.popular_comments = s.movieDetailData.popular_comments;
      s.trailer_urls = s.movieDetailData.trailer_urls;

      // console.log('s.trailer_urls ==> ', s.trailer_urls);
      
    },
  
    //修改宽度
    changeW(s, w) {

      s.movieDetailData.w = w + 1;

      // :style="{width: movieDetailData.casts.length * movieDetailData.w +'px'}"
      // s.actorliseW= s.movieDetailData.casts.length * w + 1;
      // console.log('s.actorliseW=>',s.actorliseW);

    },
  
    // 修改行数
    changeDisplay(s) {
      // console.log('展开');
      s.display = s.display == 'block' ? '-webkit-box' : 'block';
    },

    // 修复详情页  重置state
    resetState(state) {
      state.movieDetailData = {
        images: {},
        durations:[],
        languages:[],
        rating: {},
        casts: [],
        actorliseW:'',
        pcomments:[],
        previews:[],
      };
      state.likeActive = false;
    },

    changelikeActive(s, isActive) {
      s.likeActive = isActive;
    },
    
  };