export const mutations = {

    getBoxOfficeData(s,o){
        s.boxOfficeData = o.concat();
        // console.log('s.boxOfficeData=>',s.boxOfficeData);

        s.boxOfficeList.end = s.boxOfficeData.length;

        s.boxOfficeList.data = s.boxOfficeData.
        slice(s.boxOfficeList.start,s.boxOfficeList.end); 

        // console.log('s.boxOfficeList.data=>',s.boxOfficeList.data);
    },

    getUSboxOfficeData(s,o){
        s.USboxOfficeData = o.concat();
        // console.log('s.USboxOfficeData=>',s.USboxOfficeData);

        s. USboxOfficeList.end = s.USboxOfficeData.length;

        s. USboxOfficeList.data = s.USboxOfficeData.
        slice(s. USboxOfficeList.start,s. USboxOfficeList.end); 

        // console.log('s. USboxOfficeList.data=>',s. USboxOfficeList.data);
       
    },
   
}