export const mutations = {
    // 获取所有数据
    getRecommendMData(s,r){

        //判断是否第一次请求数据
        if (s.isInit) {
            s.isInit = false;
        }
        s.recommendMovieData = r.concat();  // .concat() 解除
        // console.log(' s.recommendMovieData=>', s.recommendMovieData);
        
        //截取数据
        s.showRecommendMData.data = s.recommendMovieData.
        slice(s.showRecommendMData.start,s.showRecommendMData.end);
        
        s.showRecommendMData.start = s.showRecommendMData.end;
        s.showRecommendMData.end += 10;
        // console.log('s.showRecommendMData.data=>',s.showRecommendMData.data);
    },

    // 展示更多数据
    toggleRecommendMData(s){
        let rdata = s.recommendMovieData.slice(s.showRecommendMData.start,s.showRecommendMData.end);
        let count = rdata.length; //获取截取数据数量

        // 如果数据不存在
        if(count == 0){
            s.isHasData = false;
        }else if(count < 10){
            //如果不足end条数据，需要将之前的数据移除前面count条数据,再往后面加入count条数据
            s.showRecommendMData.data.splice(0,count);
            s.showRecommendMData.data.push(...rdata);

            s.isHasData = false;
        }else{
            s.showRecommendMData.data = rdata;
        }

        if(!s.isHasData){
            // console.log('回来');
            s.showRecommendMData.data = s.recommendMovieData.
            slice(s.showRecommendMData.start,s.showRecommendMData.end);
        }
    },

    // 正在上映电影数据
    getNewMData(s,r){
        s.newMovieData = r.concat();
        // console.log('s.newMovieData=>',s.newMovieData);
        
        //截取数据
        s.showNewMData.data = s.newMovieData.slice(s.showNewMData.start,s.showNewMData.end);
        s.showNewMData.start = s.showNewMData.end;
        s.showNewMData.end += 8;
        // console.log('s.showNewMData.data=>',s.showNewMData.data);
    },

    // 即将上映电影数据
    getComingMData(s,r){
        s.comingMovieData = r.concat();
        // console.log('s.comingMovieData=>',s.comingMovieData);
        
        //截取数据
        s.showComingMData.data = s.comingMovieData.slice(s.showComingMData.start,s.showComingMData.end);
        s.showComingMData.start = s.showComingMData.end;
        s.showComingMData.end += 10;
        // console.log('s.showComingMData.data=>',s.showComingMData.data);

        s.comingMovieData.forEach(v => {
            // console.log('v=>',v.id);
            s.detailDataId.push(v.id);
        });
        // console.log('s.detailDataId=>',s.detailDataId);
    }, 

    // 换一批数据
    toggleComingMData(s){    
        let cdata = s.comingMovieData.slice(s.showRecommendMData.start,s.showRecommendMData.end);
        let count = cdata.length; //获取截取数据数量

        // 如果数据不存在
        if(count == 0){
            s.isHasData = false;
        }else if(count < 10){
            //如果不足end条数据，需要将之前的数据移除前面count条数据,再往后面加入count条数据
            s.showComingMData.data.splice(0,count);
            s.showComingMData.data.push(...cdata);

            s.isHasData = false;
        }else{
            s.showComingMData.data = cdata;
        }

        if(!s.isHasData){
            // console.log('回来');
            s.showComingMData.data = s.comingMovieData.
            slice(s.showComingMData.start,s.showComingMData.end);
        }
    },

    toggleNewMData(s){    

        let ndata = s.newMovieData.slice(s.showNewMData.start,s.showNewMData.end);
        let ncount = ndata.length;

        // 如果数据不存在
        if(ncount == 0){
            s.isHasData = false;
        }else if(ncount <6){
            //如果不足end条数据，需要将之前的数据移除前面count条数据,再往后面加入count条数据
            s.showNewMData.data.splice(0,count);
            s.showNewMData.data.push(...cdata);

            s.isHasData = false;
        }else{
            s.showNewMData.data = ndata;
        }

        if(!s.isHasData){
            s.showNewMData.data = s.newMovieData.
            slice(s.showNewMData.start,s.showNewMData.end);
        }
    },

    // 相似方法统一
    getMovieData(s,d,v){

    },

    toggleMovieData(s,d,v){
        
    },

    // 添加width属性
    changeMovielistWidth(s,width){
        //根据星级平均分设置width
        s.newMovieData.forEach((v) => {
            v.width = v.rating.average / v.rating.max * width;
        })
    },

    // 修改定位城市
    changeLocationCity(s, city) {
        s.cities.city = city;
        // console.log('s.cities.city ==> ', s.cities.city);
    },

}