export const mutations = {

    validphone(s,isValid){
        // console.log('isValid=>',isValid); //查看此次状态
        s.phone.errorMsg = isValid ? '' : '请输入正确的手机号码';
    },

    verificationCode(s){
       
        // console.log('ran=>',ran);
        for(let i = 0; i<4; i++){
            let ran =Math.floor(Math.random()*10);
            s.vefcode.value = ran;
            s.vefcode.arr.splice(i,1,ran);
            // s.vefcode.arr.join("" );
            // s.vefcode.value.;
        }
        
        // console.log('s.vefcode.arr',s.vefcode.arr);
        

        // s.vefcode.value.splice(0); //第二个参数不写的时候,这样则是删除第一个参数为索引后面的所有项
    },

    validcode(s){

        let isValid = false;

        // console.log('s.vefcode.arr=>',s.vefcode.arr); //查看此次状态
    
        let codearr = s.vefcode.arr.join("");
        // console.log('codearr=>',codearr);

        if(s.vefcode.value == codearr){
            isValid = true;
        }
        s.vefcode.errorMsg = isValid ? '' : '验证码不正确';
        
        // console.log('isValid=>',isValid); //查看此次状态
    },

    passFun(state){
        state.isValid = (state.phone.errorMsg == '' && state.phone.value != '') 
        &&(state.vefcode.errorMsg == '' && state.vefcode.value != '')
    },

    login(state,pass){
        state.isLoading = pass;
    }

}