export const mutations = {
    // 类似vue的methods 放方法  s=>state
    // 查看密码
    checkPwd(s,key){
        // console.log('state=>',s);
        // console.log('key=>',key); //key 参数：pwd/repwd
        // console.log(s[key].value);
        // 密码查看
        // s[key].type = s[key].type === 'password' ? 'text' : 'password';

        // 修改其他状态  
        let isView = s[key].type === 'password' ;
        s[key].type = isView ? 'text':'password'; // 密码查看
        s[key].icon = isView ? 'eye-o' : 'closed-eye'; //眼睛icon变化
    },

    //验证通过 点击注册
    register(s,pass){
        // 先默认通过
        // s.isLoading = true;
        s.isLoading = pass;
    },

    // 验证用户名  valid单词有效的
    validUsername(s,isValid){
        // console.log('isValid=>',isValid); //查看此次状态

        // 用户名输入错误提示 
        s.user.errorMsg = isValid ? '' : '请输入4-8个字母数字组合';
    },

    // 密码验证
    validPwd(s,isValid){
        // console.log('isValid=>',isValid); //查看此次状态
        s.pwd.errorMsg = isValid ? '' : '请输出6-16位大小写字母和数字结合的密码';
    },

    // 合并方式：用户名与密码验证
    validCommon(s,o){
        s[o.key].errorMsg = o.isValid ? '' : o.errorMsg;
    },

    // 密码一致验证
    validThesame(s,isValid){
        s.repwd.errorMsg = isValid ? '' : '密码不一致';
    },

    // 判断是否全部输入都满足条件 全部满足就开启注册按钮的:disabled="!isValid"让其可以点击
    // (判断errorMsg的值都为空,内容不能为空即满足条件）即总的isValid能为true,在Rv里每个input的事件即没输入一个就调用一次
    passFun(state){
        state.isValid = (state.user.errorMsg == '' && state.user.value != '') &&
        (state.pwd.errorMsg == '' && state.pwd.value != '') &&
        (state.repwd.errorMsg == '' && state.repwd.value != '')
    }

}                  