export const mutations = {

    // 验证用户名和密码
    validCommon(state,o){
        // state[o.key].errorMsg = o.isValid ? '' : o.errorMsg;
        state[o.key].errorMsg = o.isValid ? '' : o.errorMsg;
    },

    checkPwd(s,key){
        let isView = s[key].type === 'password' ;
        s[key].type = isView ? 'text':'password'; // 密码查看
        s[key].icon = isView ? 'eye-o' : 'closed-eye'; //眼睛icon变化
    },

    passFun(state){
        state.isValid = (state.user.errorMsg == '' && state.user.value != '') 
        &&(state.pwd.errorMsg == '' && state.pwd.value != '')
    },

    login(state,pass){
        state.isLoading = pass;
    }

}