// class方式导入导出
class RoutesComponent{

  registerComponent() {
    return import('../views/Register.vue')
  }

  loginComponent() {
    return import('../views/Login.vue')
  }
  forgetPwdComponent() {
    return import('../views/ForgetPwd.vue')
  }


  // 手机注册组件
  phoneRegisterComponent() {
    return import('../views/PhoneRegister.vue')
  }

  // index
  indexComponent() {
    return import('../views/Index.vue')
  }

  homeComponent() {
    return import('../views/indexComponent/Home.vue')
  }

  movieComponent() {
    return import('../views/indexComponent/Movie.vue')
  }
  
  findComponent() {
    return import('../views/indexComponent/Find.vue')
  }

  showComponent() {
    return import('../views/indexComponent/Show.vue')
  }

  mineComponent() {
    return import('../views/indexComponent/Mine.vue')
  }

  // movieComponent下组件
  movielistComponent() {
    return import('../views/movieComponent/Movielist.vue')
  }

  cinemaComponent() {
    return import('../views/movieComponent/Cinema.vue')
  }
  
  comingComponent() {
    return import('../views/movieComponent/Coming.vue')
  }


  // 一级路由
  movieDetailComponent() {
    return import('../views/MovieDetail.vue')
  }

  likeMovieComponent() {
    return import('../views/LikeMovie.vue')
  }

  buyticketsComponent() {
    return import('../views/BuyTickets.vue')
  }

  searchMovieComponent() {
    return import('../views/Searchmovie.vue')
  }
  
  // 首页跳转一级路由
  teaserComponent() {
    return import('../views/homeComponent/Teaser.vue')
  }

  boxOfficeComponent() {
    return import('../views/homeComponent/BoxOffice.vue')
  }

  rankingListComponent() {
    return import('../views/homeComponent/RankingList.vue')
  }

  mallComponent() {
    return import('../views/homeComponent/Mall.vue')
  }

  ticketsComponent() {
    return import('../views/Tickets.vue')
  }

}

export default new RoutesComponent();

